@font-face {
  font-family: 'Lato-Bold';
  src: url('../data/fonts/Lato-Bold/Lato-Bold.eot');
  src: url('../data/fonts/Lato-Bold/Lato-Bold.woff2') format('woff2'),
       url('../data/fonts/Lato-Bold/Lato-Bold.woff') format('woff'),
       url('../data/fonts/Lato-Bold/Lato-Bold.ttf') format('truetype'),
       url('../data/fonts/Lato-Bold/Lato-Bold.svg#Lato-Bold') format('svg'),
       url('../data/fonts/Lato-Bold/Lato-Bold.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}

